body{
  margin: 0%;
  padding: 0%;
  background-color: #116A7B;
}

h1{
  padding-top: 3%;
  font-size: 3vw;
  text-align: center;
  color: #F4F2DE;
  font-family: Arial;
}

.center{
  text-align: center;
}

.selector{
  text-align: center;
  font-weight: 160 !important;
  font-size: 2.5vw;
  padding: 5px 20px;
  background-color:#ebe9e4;
  border: white;
  color: #116A7B;
}

@media screen and (min-width: 760px) {
  .selector {
     font-size: 20px;
     max-height: 35px;
  }
}

button{
  text-align: center;
  margin: 2.2% auto;
  display: block;
  padding: 5px 20px;
  font-weight: 160 !important;
  font-size: 2.5vw;
  border:#CDC2AE;
  color:#ab9b7d;
  background-color: #ebe9e4;
}

@media screen and (min-width: 760px) {
  button {
     font-size: 20px;
     max-height: 35px;
     margin: 20px auto;
  }
}

.prompt{
  width: 45%;
  margin: 4% auto;
  display: block;
  border: 2px rgb(75, 93, 75);
  background-color: aliceblue;
  padding: 5%;
  text-align: center;
  font-size: 3vw;
  font-weight: 160;

}

@media screen and (min-width: 760px) {
  .prompt {
     font-size: 23px;
     width: 350px;
     margin: 25px auto;
  }
}

img{
  width: 100%;
  max-width: 500px;
}

.date{
  text-align: center;
  font-size: 4vw;
  color: #CDC2AE;
  margin: 4%;
  font-weight: 150;
}
@media screen and (min-width: 700px) {
  .date {
     font-size: 28px;
     margin-top: 4%;
     margin-bottom: 28px;
  }
}